import { Button } from 'reactstrap';
import { useScrollStore } from '../../libs/stores';

/**
 *
 * @param props {{scrollPos: number | undefined}}
 * @return {JSX.Element}
 * @constructor
 */
export default function ScrollToTopButton(props) {
  const scrollPosition = useScrollStore(state => state.scrollPosition);
  const scrollBarWidth = useScrollStore(state => state.scrollBarWidth);

  const scrollPos = props.scrollPos ?? 0;

  // console.log(scrollPos, scrollPosition, scrollBarWidth);

  return <Button
    className="scroll-top-button"
    onClick={() => {
      document.body.scrollTo({
        top: scrollPos,
        behavior: "smooth",
      });
    }}
    style={{
      margin: "0",
      padding: "8px",
      position: "fixed",
      bottom: "10px",
      right: `${10 + scrollBarWidth}px`,
      zIndex: "1050",
      display: scrollPos < scrollPosition ? "flex" : "none",
    }}
  >
    <i className="fas fa-arrow-up"></i>
  </Button>
}